// Flex 
.flex {
    display: flex;
}

.justify-center{
    justify-content: center;
}

.space-between{
    justify-content: space-between;
}

.align-center{
    align-items: center;
}

.column {
    flex-direction: column;
}

// List 
.clean-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

